/* **************** HEADER ****************** */
.page-header {
  background-color: #29b6f6;
  color: #fff; }
  .page-header .container {
    display: flex;
    justify-content: space-between;
    align-items: center; }

.brand-logo {
  color: #fff;
  display: inline-block;
  font-size: 2.1rem;
  padding: 0; }
  .brand-logo:hover {
    text-decoration: none; }

.header-navbar__link {
  transition: background-color .3s;
  font-size: 1rem;
  color: #fff;
  display: block;
  padding: 23px 15px; }
  .header-navbar__link:hover {
    background-color: rgba(0, 0, 0, 0.1);
    text-decoration: none; }
